.let{
    padding-top: 200px;
    padding-bottom: 100px;
    opacity: 0.99;
    color: white;
    overflow: hidden;
    border-right: 2px solid orange;
    
    white-space: nowrap; 
    margin: 0 auto; 
    letter-spacing: 10px;
    animation: 
      let 3.5s steps(10, end),
      blink-caret .75s step-start infinite;
      animation-iteration-count: infinite;

}
@keyframes let {
    from { width: 0 }
    to { width: 50% }
  }
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: peru; }
  }
